// Preview.js
import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import { HY_BRANCHES, formatDocNo } from "./hyBranches";

export default function Preview() {
  const location = useLocation();
  const navigate = useNavigate();

  const formData = location.state?.formData || {};
  const costs = location.state?.costs || {};
  const branch = HY_BRANCHES[formData.branchKey || "IC"]; // Í∏∞Î≥∏ Ïù∏Ï≤ú

  // Î¨∏ÏÑú ÌÉÄÏûÖ: invoice | quotation
  const docType = formData.docType || "invoice";
  const title = docType === "quotation" ? "Quotation" : "Invoice";
  const fileLabel = docType === "quotation" ? "Í≥ºÏÑ∏ÏûêÎ£å Ïª®ÏÑ§ÌåÖ Í≤¨Ï†ÅÏÑú" : "Í≥ºÏÑ∏ÏûêÎ£å Ïª®ÏÑ§ÌåÖ Ï≤≠Íµ¨ÏÑú";

  const today = new Date();
  const dateString = `${String(today.getFullYear()).slice(2)}${String(
    today.getMonth() + 1
  ).padStart(2, "0")}${String(today.getDate()).padStart(2, "0")}`;

  const downloadPDF = () => {
    const doc = new jsPDF("p", "pt", "a4");
    const input = document.getElementById("invoice-content");

    const companyNameSafe = formData.company
      ? formData.company.replace(/[^Í∞Ä-Ìû£a-zA-Z0-9\s]/g, "")
      : "ÏóÖÏ≤¥Î™ÖÏóÜÏùå";
    const filename = `[Í¥ÄÏÑ∏Î≤ïÏù∏ÌïúÏòÅ] ${companyNameSafe} ${fileLabel}_${dateString}.pdf`;

    html2canvas(input, { scale: 2 }).then((canvas) => {
      const imgData = canvas.toDataURL("image/png");
      const pdfWidth = doc.internal.pageSize.getWidth() - 40;
      const imgProps = doc.getImageProperties(imgData);
      const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;

      doc.addImage(imgData, "PNG", 20, 20, pdfWidth, pdfHeight);
      doc.save(filename);
    });
  };

  const amountMap4 = {
    1: "100Ïñµ Ïù¥Ìïò",
    2: "100Ïñµ Ï¥àÍ≥º 200Ïñµ Ïù¥Ìïò",
    3: "200Ïñµ Ï¥àÍ≥º 300Ïñµ",
    4: "300Ïñµ Ï¥àÍ≥º",
  };

  const reportItemLabels = {
    royalty: "Î°úÏó¥Ìã∞",
    productionSupport: "ÏÉùÏÇ∞ÏßÄÏõêÎπÑ",
    freightInsurance: "Ïö¥ÏûÑ/Î≥¥ÌóòÎ£å",
    commission: "ÏàòÏàòÎ£å/Ï§ëÍ∞úÎ£å",
    packaging: "Ïö©Í∏∞/Ìè¨Ïû•ÎπÑ",
    postBenefit: "ÏÇ¨ÌõÑÍ∑ÄÏÜçÏù¥Ïùµ",
    indirectPayment: "Í∞ÑÏ†ëÏßÄÍ∏âÍ∏àÏï°",
    other: "Í∏∞ÌÉÄ",
  };

  const serviceItems = [
    {
      label: "Í±∞ÎûòÍµ¨Ï°∞ Î∞è Í≥ºÏÑ∏ÏûêÎ£å Ï†úÏ∂ú Î≤îÏúÑ Í≤ÄÌÜ†",
      checked: formData.includeBase,
      quantity: formData.partners || 0,
      total: costs.baseCost,
      details: `Ïó∞ ÏàòÏûÖÍ∏àÏï°: ${amountMap4[formData.importAmount] || "ÎØ∏ÏûÖÎ†•"}`,
    },
    {
      label: "Ìï¥Ïô∏Í±∞ÎûòÏ≤òÎ≥Ñ Í≥ºÏÑ∏ÏûêÎ£å Ï†úÏ∂ú Î≤îÏúÑ Í≤ÄÌÜ†",
      checked: formData.includeOverseasRange,
      quantity: formData.overseasPartners || 0,
      total: costs.overseasRangeCost,
      details: ``,
    },
    {
      label: "Í≥ºÏÑ∏ÏûêÎ£å ÎØ∏Ï†úÏ∂ú ÏÇ¨Ïú†ÏÑú ÏûëÏÑ± ÏßÄÏõê",
      checked: formData.includeReason,
      quantity: formData.reasonPartners || 0,
      total: costs.reasonCost,
      details: ``,
    },
    {
      label: "Í≥ºÏÑ∏ÏûêÎ£å ÏßÄÏó∞Ï†úÏ∂ú ÏÇ¨Ïú†ÏÑú ÏûëÏÑ± ÏßÄÏõê",
      checked: formData.includeDelayReason,
      quantity: formData.delayReasonPartners || 0,
      total: costs.delayReasonCost,
      details: ``,
    },
    {
      label: "Í≥ºÏÑ∏ÏûêÎ£å Ï†úÏ∂ú ÏÑúÎ•ò Ï†ïÎπÑ Î∞è Ï†ÑÏûêÏ†úÏ∂ú ÎåÄÌñâ",
      checked: formData.includeDoc,
      quantity: formData.docPartners || 0,
      total: costs.docCost,
      details: ``,
    },
    {
      label: "Í≥ºÏÑ∏Í∞ÄÍ≤© Í≤∞Ï†ï ÏÇ¨Ïú† Î≥¥Í≥†ÏÑú ÏûëÏÑ±",
      checked: formData.includeReport,
      quantity:
        Object.values(formData.reportItems || {}).filter(Boolean).length *
        (formData.reportPartners || 0),
      total: costs.reportCost,
      details: `${
        Object.entries(formData.reportItems || {})
          .filter(([_, v]) => v)
          .map(([k]) => reportItemLabels[k] || k)
          .join(", ") || "ÏóÜÏùå"
      }`,
    },
    {
      label: "ÌäπÏàòÍ¥ÄÍ≥ÑÏûê Í±∞Îûò Î∂ÑÏÑù Î∞è Ïù¥Ï†ÑÍ∞ÄÍ≤© Í≤ÄÌÜ† Î¶¨Ìè¨Ìä∏",
      checked: formData.includeRelated,
      quantity: "-",
      total: costs.relatedCost,
      details: `Ïó∞ ÏàòÏûÖÍ∏àÏï°: ${amountMap4[formData.relatedImportAmount] || "ÎØ∏ÏûÖÎ†•"}`,
    },
    {
      label: formData.includeCustom ? (formData.customLabel || "Í≥µÎûÄ") : "Í≥µÎûÄ",
      checked: formData.includeCustom,
      quantity: formData.customQty || 0,
      total: costs.customCost,
      details: ``,
    },
  ];

  const selectedServices = serviceItems.filter((i) => i.checked);

  return (
    <>
      <div
        id="invoice-content"
        style={{
          fontFamily: "'Pretendard', 'Helvetica Neue', sans-serif",
          backgroundColor: "#fff",
          color: "#222",
          padding: 60,
          maxWidth: 800,
          margin: "60px auto",
          lineHeight: 1.7,
          border: "1px solid #ddd",
          minHeight: "1122px",
          boxSizing: "border-box",
          display: "flex",
          flexDirection: "column",
          justifyContent: "space-between",
        }}
      >
        {/* ÏÉÅÎã® Ìó§Îçî */}
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            marginBottom: 60,
          }}
        >
          <div>
            <h1 style={{ fontSize: 32, fontWeight: 700, marginBottom: 10 }}>
              {title}
            </h1>
            <p style={{ fontSize: 12, color: "#555" }}>
              No. {formatDocNo(branch.codePrefix, formData.doc_number)} &nbsp; / &nbsp;{" "}
              {today.toLocaleDateString("ko-KR")}
            </p>
          </div>
          <div style={{ width: 170, height: "auto" }}>
            <img
              src={`${process.env.PUBLIC_URL}/images/HYLOGO_NAVY.png`}
              alt="ÌïúÏòÅ Î°úÍ≥†"
              style={{ width: "100%", height: "auto", objectFit: "contain" }}
            />
          </div>
        </div>

        {/* ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏/Î∞úÌñâÏ≤ò */}
        <div style={{ display: "flex", justifyContent: "space-between", marginBottom: 40 }}>
          <div>
            <strong>Client</strong>
            <p style={{ fontSize: 14 }}>{formData.company || "Client Name"}</p>
          </div>
          <div style={{ textAlign: "right" }}>
            <strong>Issued By</strong>
            <p style={{ fontSize: 14 }}>Í¥ÄÏÑ∏Î≤ïÏù∏ ÌïúÏòÅ ({branch.label})</p>
          </div>
        </div>

        {/* ÏÑúÎπÑÏä§ ÌÖåÏù¥Î∏î */}
<table style={{ width: "100%", borderCollapse: "collapse", marginBottom: 40, fontSize: 12 }}>
          <thead>
            <tr style={{ borderBottom: "1px solid #ccc", backgroundColor: "#f9f9f9" }}>
              <th style={{ padding: "12px 8px", textAlign: "center" }}>Description</th>
              <th style={{ padding: "12px 8px", textAlign: "center" }}>Details</th>
              <th style={{ padding: "12px 8px", textAlign: "center" }}>Overseas Partners</th>
              <th style={{ padding: "12px 8px", textAlign: "center" }}>Amount (‚Ç©)</th>
            </tr>
          </thead>
          <tbody>
            {selectedServices.map((item, idx) => (
              <tr key={idx} style={{ borderBottom: "1px solid #eee" }}>
                <td style={{ padding: "12px 8px", textAlign: "center" }}>{item.label}</td>
                <td style={{ padding: "12px 8px", textAlign: "center" }}>{item.details}</td>
                <td style={{ padding: "12px 8px", textAlign: "center" }}>{item.quantity}</td>
                <td style={{ padding: "12px 8px", textAlign: "center" }}>
                  {item.total?.toLocaleString?.() ?? "0"}
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {/* Î©îÎ™® & Ìï©Í≥Ñ */}
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            fontSize: 14,
            marginBottom: 40,
          }}
        >
          <div>
            <strong>Note</strong>
            <p style={{ whiteSpace: "pre-line" }}>{formData.note?.trim() || ""}</p>
          </div>

          <div style={{ textAlign: "right" }}>
            {Number(formData.discountRate) > 0 && (
              <p>
                Discount: {formData.discountRate}% (
                {costs.discountAmount?.toLocaleString() ?? "0"} Ïõê)
              </p>
            )}
            <p>VAT (10%): {costs.vat?.toLocaleString?.() ?? "0"} Ïõê</p>
            <h3 style={{ fontSize: 16, marginTop: 10 }}>
              Grand Total: {costs.totalCost?.toLocaleString?.() ?? "0"} Ïõê
            </h3>
          </div>
        </div>

        {/* ÏßÄÏÇ¨Î≥Ñ ÌïòÎã® Ï†ïÎ≥¥: Ï¢å(ÏùÄÌñâÏ†ïÎ≥¥) / Ïö∞(Ïú†Ìö®Í∏∞Í∞Ñ¬∑Ï£ºÏÜå¬∑TEL¬∑FAX) */}
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "flex-start",
            gap: 24,
            fontSize: 12,
          }}
        >
          {/* Left: Bank info */}
          <div style={{ lineHeight: 1.3 }}>
            <p>
              <strong>Bank:</strong> {branch.bank}
            </p>
            <p>
              <strong>Account:</strong> {branch.account}
            </p>
            <p>
              <strong>Holder:</strong> {branch.holder}
            </p>
          </div>

          {/* Right: Valid Until, Address, TEL/FAX */}
          <div
            style={{
              textAlign: "right",
              fontSize: 12,
              color: "#666",
              lineHeight: 1.3,
              whiteSpace: "pre-line",
            }}
          >
            <p>
              <strong>Valid Until:</strong>{" "}
              {new Date(today.getTime() + 15 * 86400000).toLocaleDateString("ko-KR")}
            </p>
            <p>{branch.address}</p>
            <p>
              TEL: {branch.tel} / FAX: {branch.fax}
            </p>
          </div>
        </div>
      </div>

      {/* Î≤ÑÌäºÎì§ */}
      <div style={{ marginTop: 40, display: "flex", justifyContent: "center", gap: 20 }}>
        <button
          onClick={() => navigate("/")}
          style={{
            backgroundColor: "#aaa",
            border: "none",
            padding: "10px 30px",
            borderRadius: 6,
            cursor: "pointer",
            fontWeight: "600",
            color: "#fff",
          }}
        >
          Îã§ÏãúÌïòÍ∏∞
        </button>
        <button
          onClick={downloadPDF}
          style={{
            backgroundColor: "#111",
            border: "none",
            padding: "10px 30px",
            borderRadius: 6,
            cursor: "pointer",
            fontWeight: "700",
            color: "#fff",
          }}
        >
          üì• PDF Îã§Ïö¥Î°úÎìú
        </button>
      </div>
    </>
  );
}
